'use client';

import { useState, useEffect, useMemo } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select';
import {
  Tabs,
  TabsContent,
  TabsList,
  TabsTrigger,
} from '@/components/ui/tabs';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table';
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from '@/components/ui/dropdown-menu';
import { subscribeToFirestore } from '@/services/firebase';
import {
  Plus, RefreshCw, MoreHorizontal, Search, Download,
  Truck, MapPin, Package, Clock, CheckCircle, AlertTriangle
} from 'lucide-react';

// Delivery status badge
function StatusBadge({ status }: { status: string }) {
  const styles: Record<string, string> = {
    'pending': 'bg-amber-100 text-amber-800',
    'in_transit': 'bg-blue-100 text-blue-800',
    'delivered': 'bg-green-100 text-green-800',
    'failed': 'bg-red-100 text-red-800',
    'cancelled': 'bg-gray-100 text-gray-600',
  };

  const labels: Record<string, string> = {
    'pending': 'Bekliyor',
    'in_transit': 'Yolda',
    'delivered': 'Teslim Edildi',
    'failed': 'Basarisiz',
    'cancelled': 'Iptal',
  };

  const icons: Record<string, React.ReactNode> = {
    'pending': <Clock className="h-3 w-3 mr-1" />,
    'in_transit': <Truck className="h-3 w-3 mr-1" />,
    'delivered': <CheckCircle className="h-3 w-3 mr-1" />,
    'failed': <AlertTriangle className="h-3 w-3 mr-1" />,
  };

  return (
    <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${styles[status] || 'bg-gray-100 text-gray-600'}`}>
      {icons[status]}
      {labels[status] || status}
    </span>
  );
}

interface Delivery {
  id: string;
  orderNo?: string;
  customerName?: string;
  address?: string;
  date?: string;
  scheduledTime?: string;
  driver?: string;
  vehicle?: string;
  status?: string;
  items?: number;
  totalWeight?: number;
  notes?: string;
  createdAt?: string;
}

interface Route {
  id: string;
  routeName?: string;
  driver?: string;
  vehicle?: string;
  date?: string;
  stops?: number;
  totalDistance?: number;
  status?: string;
}

export default function LogisticsPage() {
  const [deliveries, setDeliveries] = useState<Delivery[]>([]);
  const [routes, setRoutes] = useState<Route[]>([]);
  const [vehicles, setVehicles] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchQuery, setSearchQuery] = useState('');
  const [statusFilter, setStatusFilter] = useState('all');
  const [dateFilter, setDateFilter] = useState('');

  // Load data from Firestore
  useEffect(() => {
    setLoading(true);

    const unsubDeliveries = subscribeToFirestore('deliveries', (data) => {
      setDeliveries(data || []);
    });

    const unsubRoutes = subscribeToFirestore('routes', (data) => {
      setRoutes(data || []);
    });

    const unsubVehicles = subscribeToFirestore('vehicles', (data) => {
      setVehicles(data || []);
      setLoading(false);
    });

    return () => {
      unsubDeliveries();
      unsubRoutes();
      unsubVehicles();
    };
  }, []);

  // Stats
  const stats = useMemo(() => {
    const today = new Date().toISOString().split('T')[0];
    const todayDeliveries = deliveries.filter(d => d.date?.startsWith(today)).length;
    const pending = deliveries.filter(d => d.status === 'pending').length;
    const inTransit = deliveries.filter(d => d.status === 'in_transit').length;
    const activeVehicles = vehicles.filter(v => v.status === 'active').length;

    return { todayDeliveries, pending, inTransit, activeVehicles };
  }, [deliveries, vehicles]);

  // Filter deliveries
  const filteredDeliveries = useMemo(() => {
    return deliveries
      .filter((del) => {
        const searchLower = searchQuery.toLowerCase();
        const matchesSearch = !searchQuery ||
          (del.orderNo || '').toLowerCase().includes(searchLower) ||
          (del.customerName || '').toLowerCase().includes(searchLower) ||
          (del.driver || '').toLowerCase().includes(searchLower);

        const matchesStatus = statusFilter === 'all' || del.status === statusFilter;
        const matchesDate = !dateFilter || (del.date && del.date.startsWith(dateFilter));

        return matchesSearch && matchesStatus && matchesDate;
      })
      .sort((a, b) => new Date(b.date || b.createdAt || '').getTime() - new Date(a.date || a.createdAt || '').getTime());
  }, [deliveries, searchQuery, statusFilter, dateFilter]);

  const handleRefresh = () => {
    setLoading(true);
    setTimeout(() => setLoading(false), 500);
  };

  return (
    <div className="h-full flex flex-col bg-gray-50">
      {/* Header */}
      <div className="bg-white border-b px-8 py-6">
        <div className="flex items-center justify-between mb-4">
          <div>
            <h1 className="text-2xl font-semibold text-gray-900">Lojistik Yonetimi</h1>
            <p className="text-sm text-gray-500 mt-1">
              Teslimat, rota ve arac takibi
            </p>
          </div>

          <div className="flex items-center gap-3">
            <Button
              variant="outline"
              size="sm"
              onClick={handleRefresh}
              disabled={loading}
            >
              <RefreshCw className={`h-4 w-4 mr-2 ${loading ? 'animate-spin' : ''}`} />
              Yenile
            </Button>
            <Button variant="outline" size="sm">
              <Download className="h-4 w-4 mr-2" />
              Rapor
            </Button>
            <Button variant="outline">
              <MapPin className="h-4 w-4 mr-2" />
              Rota Olustur
            </Button>
            <Button>
              <Plus className="h-4 w-4 mr-2" />
              Teslimat Ekle
            </Button>
          </div>
        </div>

        {/* Stats Cards */}
        <div className="grid grid-cols-4 gap-4 mb-6">
          <div className="bg-blue-50 rounded-lg p-4">
            <div className="flex items-center gap-3">
              <div className="p-2 bg-blue-100 rounded-lg">
                <Package className="h-5 w-5 text-blue-600" />
              </div>
              <div>
                <p className="text-sm text-gray-600">Bugun Teslimat</p>
                <p className="text-xl font-semibold text-gray-900">{stats.todayDeliveries}</p>
              </div>
            </div>
          </div>
          <div className="bg-amber-50 rounded-lg p-4">
            <div className="flex items-center gap-3">
              <div className="p-2 bg-amber-100 rounded-lg">
                <Clock className="h-5 w-5 text-amber-600" />
              </div>
              <div>
                <p className="text-sm text-gray-600">Bekleyen</p>
                <p className="text-xl font-semibold text-amber-600">{stats.pending}</p>
              </div>
            </div>
          </div>
          <div className="bg-green-50 rounded-lg p-4">
            <div className="flex items-center gap-3">
              <div className="p-2 bg-green-100 rounded-lg">
                <Truck className="h-5 w-5 text-green-600" />
              </div>
              <div>
                <p className="text-sm text-gray-600">Yolda</p>
                <p className="text-xl font-semibold text-green-600">{stats.inTransit}</p>
              </div>
            </div>
          </div>
          <div className="bg-purple-50 rounded-lg p-4">
            <div className="flex items-center gap-3">
              <div className="p-2 bg-purple-100 rounded-lg">
                <Truck className="h-5 w-5 text-purple-600" />
              </div>
              <div>
                <p className="text-sm text-gray-600">Aktif Arac</p>
                <p className="text-xl font-semibold text-purple-600">{stats.activeVehicles}</p>
              </div>
            </div>
          </div>
        </div>

        {/* Filters */}
        <div className="flex items-center gap-4">
          <div className="relative flex-1 max-w-md">
            <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-gray-400" />
            <Input
              placeholder="Teslimat ara..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="pl-10"
            />
          </div>

          <Input
            type="date"
            value={dateFilter}
            onChange={(e) => setDateFilter(e.target.value)}
            className="w-[180px]"
          />

          <Select value={statusFilter} onValueChange={setStatusFilter}>
            <SelectTrigger className="w-[150px]">
              <SelectValue placeholder="Durum" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="all">Tum durumlar</SelectItem>
              <SelectItem value="pending">Bekliyor</SelectItem>
              <SelectItem value="in_transit">Yolda</SelectItem>
              <SelectItem value="delivered">Teslim Edildi</SelectItem>
              <SelectItem value="failed">Basarisiz</SelectItem>
            </SelectContent>
          </Select>
        </div>
      </div>

      {/* Content */}
      <div className="flex-1 overflow-auto p-8">
        <Tabs defaultValue="deliveries" className="w-full">
          <TabsList className="grid w-full grid-cols-3 mb-6 max-w-lg">
            <TabsTrigger value="deliveries">Teslimatlar</TabsTrigger>
            <TabsTrigger value="routes">Rotalar</TabsTrigger>
            <TabsTrigger value="vehicles">Araclar</TabsTrigger>
          </TabsList>

          {/* Deliveries Tab */}
          <TabsContent value="deliveries">
            <div className="bg-white rounded-lg border">
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Siparis No</TableHead>
                    <TableHead>Musteri</TableHead>
                    <TableHead>Adres</TableHead>
                    <TableHead>Tarih</TableHead>
                    <TableHead>Sofor</TableHead>
                    <TableHead>Arac</TableHead>
                    <TableHead>Durum</TableHead>
                    <TableHead className="w-[60px]"></TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {loading ? (
                    <TableRow>
                      <TableCell colSpan={8} className="text-center py-8 text-gray-500">
                        Yukleniyor...
                      </TableCell>
                    </TableRow>
                  ) : filteredDeliveries.length === 0 ? (
                    <TableRow>
                      <TableCell colSpan={8} className="text-center py-8 text-gray-500">
                        Teslimat bulunamadi
                      </TableCell>
                    </TableRow>
                  ) : (
                    filteredDeliveries.slice(0, 50).map((del) => (
                      <TableRow key={del.id} className="hover:bg-gray-50">
                        <TableCell className="font-mono font-medium">{del.orderNo || '-'}</TableCell>
                        <TableCell className="font-medium">{del.customerName || '-'}</TableCell>
                        <TableCell className="text-gray-600 text-sm max-w-[200px] truncate">
                          {del.address || '-'}
                        </TableCell>
                        <TableCell>{del.date || '-'}</TableCell>
                        <TableCell>{del.driver || '-'}</TableCell>
                        <TableCell>{del.vehicle || '-'}</TableCell>
                        <TableCell>
                          <StatusBadge status={del.status || 'pending'} />
                        </TableCell>
                        <TableCell>
                          <DropdownMenu>
                            <DropdownMenuTrigger asChild>
                              <Button variant="ghost" size="icon" className="h-8 w-8">
                                <MoreHorizontal className="h-4 w-4" />
                              </Button>
                            </DropdownMenuTrigger>
                            <DropdownMenuContent align="end">
                              <DropdownMenuItem>Detaylar</DropdownMenuItem>
                              <DropdownMenuItem>Duzenle</DropdownMenuItem>
                              <DropdownMenuItem>Teslim Et</DropdownMenuItem>
                              <DropdownMenuItem className="text-red-600">Iptal</DropdownMenuItem>
                            </DropdownMenuContent>
                          </DropdownMenu>
                        </TableCell>
                      </TableRow>
                    ))
                  )}
                </TableBody>
              </Table>
            </div>
          </TabsContent>

          {/* Routes Tab */}
          <TabsContent value="routes">
            <div className="bg-white rounded-lg border p-8 text-center">
              <MapPin className="h-12 w-12 mx-auto text-gray-400 mb-4" />
              <h3 className="text-lg font-medium text-gray-900 mb-2">Rota Planlama</h3>
              <p className="text-gray-500 mb-4">
                Teslimat rotalarini olusturun ve optimize edin
              </p>
              <Button>
                <Plus className="h-4 w-4 mr-2" />
                Yeni Rota
              </Button>
            </div>
          </TabsContent>

          {/* Vehicles Tab */}
          <TabsContent value="vehicles">
            <div className="bg-white rounded-lg border">
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Plaka</TableHead>
                    <TableHead>Marka/Model</TableHead>
                    <TableHead>Sofor</TableHead>
                    <TableHead>Durum</TableHead>
                    <TableHead className="text-right">Kilometre</TableHead>
                    <TableHead className="w-[60px]"></TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {vehicles.length === 0 ? (
                    <TableRow>
                      <TableCell colSpan={6} className="text-center py-8 text-gray-500">
                        Arac bulunamadi
                      </TableCell>
                    </TableRow>
                  ) : (
                    vehicles.map((vehicle) => (
                      <TableRow key={vehicle.id} className="hover:bg-gray-50">
                        <TableCell className="font-mono font-medium">{vehicle.plate || '-'}</TableCell>
                        <TableCell>{vehicle.brand} {vehicle.model}</TableCell>
                        <TableCell>{vehicle.assignedTo || '-'}</TableCell>
                        <TableCell>
                          <span className={`inline-flex items-center px-2 py-0.5 rounded-full text-xs font-medium ${
                            vehicle.status === 'active' ? 'bg-green-100 text-green-800' : 'bg-gray-100 text-gray-600'
                          }`}>
                            {vehicle.status === 'active' ? 'Aktif' : 'Pasif'}
                          </span>
                        </TableCell>
                        <TableCell className="text-right">{vehicle.km?.toLocaleString() || 0} km</TableCell>
                        <TableCell>
                          <DropdownMenu>
                            <DropdownMenuTrigger asChild>
                              <Button variant="ghost" size="icon" className="h-8 w-8">
                                <MoreHorizontal className="h-4 w-4" />
                              </Button>
                            </DropdownMenuTrigger>
                            <DropdownMenuContent align="end">
                              <DropdownMenuItem>Detaylar</DropdownMenuItem>
                              <DropdownMenuItem>Duzenle</DropdownMenuItem>
                              <DropdownMenuItem>Bakim Ekle</DropdownMenuItem>
                            </DropdownMenuContent>
                          </DropdownMenu>
                        </TableCell>
                      </TableRow>
                    ))
                  )}
                </TableBody>
              </Table>
            </div>
          </TabsContent>
        </Tabs>
      </div>
    </div>
  );
}
